body {
  background-color: #17131B;
  margin: 0px;
  padding: 0px;
}

.logo {
  position: absolute;
  width: 43vh;
  top: 19vh;
  left: 38vh;
}



.login {
  display: flex;


}

.image {
  width: 106.5vh;

}

.login_card, .register_card {
  width: fit-content;
  text-align: center;
  flex: none;
  margin: 20.9vh 0vh 0vh 0vh;
}

.login_t, .register_t {
  text-align: center;
  margin: 0px;
  width: 100%;
  height: 44px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 29px;
  line-height: 44px;
  color: #FFFFFF;
  text-align: center;
}

.to_your_account, .signup_mlangles {
  text-align: center;
  margin: 0px;
  width: 100%;
  height: 24px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #EE691E
}

.login_username {
  font-family: 'Montserrat';
  width: 368px;
  border: none;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 22px;
  border-radius: 8px;
  height: 48px;
  padding: 0px 31px;
  color: #525252;
  background: #232025;
  text-align: center;
  opacity: 0.6;
}

.login_username:focus {
  outline: none;
  opacity: 1;
}

.remember {
  padding: 0px 0px 0px 4px;
  width: 112px;
  height: 20px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 20px;
  float: left;

  color: #626262;
}

.in_input_fields {
  margin: 10px 0px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.forgot {
  font-size: 12px;
  text-decoration: none;
  color: #5A47E3;
  font-family: 'Montserrat';
  font-style: normal;
}

.checkbox {
  float: left;
}

.login_btn, .register_btn{
  padding: 11px 45px;
  border-radius: 8px;
  background: linear-gradient(15deg, #5A47E3 0.03%, #BC1886 26.02%, #EE0037 55.01%, #EF232C 60.01%, #F1501F 67.01%, #F37613 73.01%, #F4940B 80.01%, #F5A904 87%, #F5B501 94%, #F6BA00 100%);
  border: none;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 13px;
  line-height: 20px;
  color: #FFFFFF;
  cursor: pointer;
  transition: 100ms;
 
}

.login_btn:active {
  transform: scale(0.98);
  transition: 100ms;
}

.not_a_member {
  margin: 2.5vh 0vh;
  width: 266px;
  height: 20px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 12.5px;
  line-height: 20px;
  width: 100%;
  color: #FFFFFF;
}

.login_title , .register_title{
  margin-top: 10.5vh;
}

.login_inputs, .register_inputs {
  margin-top: 6.5vh;
}

.login_section {
  padding: 5.5vh 0vh;
}

.images {
  position: relative;
  width: 63vh;
  padding: 26.5vh 0vh 0vh 26vh
}

.balls {
  position: absolute;
  width: 61vh;
  left: 201px;
  top: 270px;
}
.register_now{
  text-decoration: none;
  color: #5A47E3;
}
.remember2{
  padding: 0px 0px 0px 4px;
  width: 112px;
  height: 20px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 20px;
  color: #626262;
}
.register_card{
  width: fit-content;
}
.inputs_one{
  display: flex;
}
.inputs_two{
  display: flex;
}
.inputs_three{
  display: flex;
}
.inputs_four{
  display: flex;
}
.register_card{
  width: 59vh;
}
.register{
  display: flex;
}
.register_login_name{
  width: 20vh;
}
.register_login_name2{
  margin-left: 2vh;
  width: 20vh;
}
.register_card{
  margin: 11.9vh 0vh 0vh 0vh;
}
.country{
  flex-grow: 0;
  padding: 0px;
  width: 14vh;
}
.mobile{
  width: 35.7vh;
}
.accept{
  position: relative;
  text-align: left;
  margin-top: 3vh;
}
.register_button{
  margin-top: 6vh;
}
.imagess{
  z-index: 11;
  position: relative;
  width: 63vh;
  padding: 35.5vh 0vh 0vh 26vh;
}
.ballss{
  position: absolute;
  width: 63vh;
  padding: 26.5vh 0vh 0vh 26vh;
  top: 0vh;
  left: 0px;
}
